.cart-item {
    box-shadow: 0 0 4px 1px rgba(0, 0, 0, .4);
    border-radius: 6px;
    background-color: white;
    margin-block: 1rem;
    padding: 1rem;
}

.cart-item .flex-spread {
    display: flex;
    justify-content: space-between;
}

.cart-item .flex {
    display: flex;
    align-items: center;
}

.cart-item .top img {
    width: 10rem;
}

.cart-item .top .side {
    display: none;
}

.cart-item .bottom .remove {
    border: 0;
    background-color: transparent;
    color: var(--color-2);
    letter-spacing: .1rem;
}

.cart-item .bottom .quantity-control {
    gap: 1rem;
}

.cart-item .bottom .quantity-control button {
    display: grid;
    place-items: center;
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
    border: 0;
    background-color: var(--color-2);
    color: white;
    border-radius: 4px;
    cursor: pointer;
    opacity: .7;
}

.cart-item .bottom .quantity-control button:hover {
    opacity: 1;
}

@media screen and (min-width: 520px) {
    .cart-item .top .price-m {
        display: none;
    }

    .cart-item .top .side {
        display: initial;
    }
}